export class FileSsisConnParmViewModel {
  /// <summary>
///  轉檔序號
/// <summary>
public exec_file_seq: number;
/// <summary>
///  ssis參數名稱
/// <summary>
public ssis_parm_name: string;
/// <summary>
///  遠端連線編號
/// <summary>
public remote_conn_id: string;
/// <summary>
///  ssis_參數類型主鍵
/// <summary>
public ssis_parm_type_key: string;
/// <summary>
///  建立者
/// <summary>
public creator: string;
/// <summary>
///  建立時間
/// <summary>
public create_time: Date;
/// <summary>
///  最後更新者
/// <summary>
public modifier: string;
/// <summary>
///  最後更新時間
/// <summary>
public last_update_time: Date;
/// <summary>
///  ssis_參數類型
/// <summary>
public ssis_parm_type: string;

/// <summary>
///  遠端連線字串
/// <summary>
public remote_conn_string: string;
}



